import * as React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import Tooltip, { tooltipClasses } from "@mui/material/Tooltip"
import { styled } from '@mui/material/styles';
import Button from "@material-ui/core/Button"
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';



const HtmlTooltip = styled(({ className, ...props }) => (
  <Tooltip {...props} classes={{ popper: className }} />
))(({ theme }) => ({
  [`& .${tooltipClasses.tooltip}`]: {
    backgroundColor: theme.palette.common.white,
    color: 'rgba(0, 0, 0, 0.87)',
    maxWidth: 220,
    boxShadow: theme.shadows[1],
    fontSize: theme.typography.pxToRem(12),
    border: '1px solid #dadde9',
  },
}));

const Header = () => (

  // daily and weekly missions (hover & links to edit)
  //! TO DO: hover pop-up, link to edit page, make the numbers variable so they can be edited 
  //! maybe make daily & weekly into svgs 
  <header
    style={{
      background: `rebeccapurple`,
      marginBottom: `1.45rem`,
    }}
    >
      <div
      style={{
        margin: `0 auto`,
        maxWidth: 960,
        padding: `1.45rem 1.0875rem`,
      }}>
      <HtmlTooltip title={
          <React.Fragment>
            <Typography color="inherit">Tooltip with HTML</Typography>
            <Box sx={{ width: '100%' }}></Box>
          </React.Fragment>
        } followCursor={true}>
        <Button component={Link} to="/page-2" variant="contained">Daily 2 / 2 <br /> Weekly 4 / 5</Button>
      </HtmlTooltip>
      </div> 
  </header>

)


export default Header
